<template>
<main>
    <Header :header='content'/>
    <form action="" class="container  mx-auto">
    <section> 
        <div> 
            <label>کد پرسنلی تحویل گیرنده</label> 
            <input type="text">
        </div>
        <div>
            <label >کد ست دریافتی</label> 
            <input type="text" >
        </div>
    </section>
    <section>
        <div> 
            <label >کد پرسنلی مسئول بسته بندی</label> 
            <input type="text" >
        </div>
        <div> 
            <label>نوع بسته بندی</label> 
            <select class="form-select" >
                <option selected>انتخاب کنید</option>
                <option value="1">سالم</option>
                <option value="2">معیوب</option>
            </select>
        </div>
    </section>     
    <section>
        <div> 
            <label >کد دستگاه بسته بندی</label> 
            <input type="text" >
        </div>    
        <div>
            <label >بارکد بسته بندی</label> 
            <p>
            <input type="text">
            <button class="btn btn-primary">تولید</button>
            </p>
        </div>
    </section>     
</form>

    <section class="container">
        <div> 
            <p class="fs-5">سیکل جاری دستگاه : {{counter}}</p>
        </div>    
        <div>  
        </div>
    </section>

    <div id="btn">
        <button class="btn btn-success"  @click="showModal" type="submit" data-bs-toggle="modal" data-bs-target="#staticBackdrop">ثبت اطلاعات</button>
        <button class="btn btn-success"  @click="counterPlus">شروع فرآیند</button>
        <button class="btn btn-primary">چاپ بارکد</button>
    </div>


    <!-- مدال ثبت اطلاعات -->
<div class="modal fade"  ref="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#F0FFFF;border:2px solid #2DA839">
      <div class="modal-header" style="border: none;">
        <img src="../../assets/check.png" alt="" style="margin:auto;width:50px;height:50px;">
      </div>
      <div class="modal-body" style="margin:auto;border:none;font-size:1.4rem"> 
        اطلاعات بسته بندی با موفقیت ثبت گردید
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button type="button" class="btn btn-primary mx-3" data-bs-dismiss="modal">بستن</button>
      </div>
    </div>
  </div>
</div>

 
</main>
</template>

<script>
import {Modal} from 'bootstrap';
import Header from '../../components/Header.vue'
export default {
    name:'PackingStation',
    components:{Header},
    data() {
        return {
            content:"بسته بندی",
            counter:0,
            modal:null,
            
        }
    },
    methods: {
      
      showModal(){
        this.modal=new Modal(this.$refs.staticBackdrop)  
        this.modal.show()
      },
      counterPlus(){
        this.counter += 1; 
      },
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/2col-form.scss'
</style>